import './App.css';
import {useEffect, useState} from "react";
import UsersList from './UsersList';
function App() {
  const [users, setUsers] = useState([])

    useEffect(()=>{
        getSignatures()
    },[])

  const getSignatures = () =>{
    fetch('http://localhost:5000/parse_signatures',)
        .then(response => response.json())
        .then(res => setUsers(res.signatures))
  }

  return (
    <>
      <thead className="u-black u-table-header u-table-header-1">
      <tr>
        <th className="u-table-cell">Уникальный идентификатор</th>
        <th className="u-table-cell">Поле данных</th>
      </tr>
      </thead>
      <tbody className="u-table-alt-palette-1-light-3 u-table-body">

      {users.map((user)=>{return <tr>
        <td className="u-table-cell">{user}</td>
          <td className="u-table-cell"><UsersList user={user} /></td>
      </tr>
      })}
      </tbody>
    </>
  );
}

export default App;
